@mixin border() {
  border: 1px solid rgb(41, 39, 39);
}
@mixin genericSizing() {
  margin-top: 10px;
}
@mixin flex($dir, $align, $justify) {
  display: flex;
  flex-direction: $dir;
  align-items: $align;
  justify-content: $justify;
}
@mixin productRow() {
  border-bottom: 1px solid black;
  max-width: calc(100% - 20px);
  margin-left: 10px;
}
